import { alpaca as Alpaca } from 'jquery';
import removeEmpties from './remove-empties';
import highlander from './highlander';

const mixins = [
  removeEmpties,
  highlander
];

function withMixins(FieldClass) {
  return mixins.reduce((Field, mixin) => {
    return Field.extend(mixin);
  }, FieldClass);
}

function register() {
  const BaseField = Alpaca.Field;

  Alpaca.Field = withMixins(BaseField);

  Object.keys(Alpaca.Fields).forEach((key) => {
    const Field = Alpaca.Fields[key];

    if (Field.prototype instanceof BaseField) {
      const MixedIn = withMixins(Field);
      const fieldType = MixedIn.prototype.getFieldType();
      Alpaca.Fields[key] = MixedIn;
      Alpaca.registerFieldClass(fieldType, MixedIn);
    }
  });
}

export default register;
