import { Node } from 'gitana';
import axios from './axios-instance';
import {
  getHeadersFromDriver,
  withAuthRetry
} from './helpers';

export function createDeploymentTarget(data, platform) {
  return new Promise((resolve, reject) => {
    platform
      .createDeploymentTarget(data)
      .then(function() {
        resolve(this.clone());
      })
      .trap((err) => {
        reject(err);
        return false;
      });
  });
}

export function queryDeploymentTargets(data, platform) {
  return new Promise((resolve, reject) => {
    platform
      .queryDeploymentTargets(data)
      .then(function() {
        resolve({
          rows: this.asArray()
            .map(x => x.json()),
          totalRows: this.totalRows()
        });
      })
      .trap((err) => {
        reject(err);
        return false;
      });
  });
}

