export function queryDeploymentStrategies(query, pagination, platform) {
  return new Promise((resolve, reject) => {
    platform
      .queryDeploymentStrategies(query, pagination)
      .then(function() {
        resolve({
          rows: this.asArray()
            .map(x => x.json()),
          totalRows: this.totalRows()
        });
      })
      .trap((err) => {
        reject(err);
        return false;
      });
  });
}

export async function deleteDeploymentStrategy(id, platform) {
  await withAuthRetry(async () => {
    const driver = platform.getDriver();
    const url = `${driver.baseURL}/deployment/strategies/${id}`;

    await axios.delete(url, {
      headers: getHeadersFromDriver(driver)
    });
  }, platform);
}

export async function deleteDeploymentTarget(id, platform) {
  await withAuthRetry(async () => {
    const driver = platform.getDriver();
    const url = `${driver.baseURL}/deployment/targets/${id}`;

    await axios.delete(url, {
      headers: getHeadersFromDriver(driver)
    });
  }, platform);
}

export async function deploy(ids, branch, deploymentStrategyId) {
  if (!ids.length) {
    return;
  }

  const payload = {
    operation: 'DEPLOY',
    strategyId: deploymentStrategyId,
    references: ids.map((id) => {
      const node = new Node(branch, {
        _doc: id
      });

      return node.ref();
    })
  };

  await withAuthRetry(async () => {
    const driver = branch.getDriver();
    const url = `${driver.baseURL}/ref/deploy`;

    await axios.post(url, payload, {
      headers: getHeadersFromDriver(driver)
    });
  }, branch);
}

export async function getTargetBranchId(branchId, platform) {
  let targetBranchId;

  const {
    rows: strategies
  } = await queryDeploymentStrategies({
    'source.config.branchRef': {
      $regex: `${branchId}$`
    }
  }, {}, platform);

  if (!(strategies && strategies.length)) {
    return targetBranchId;
  }

  const [strategy] = strategies;
  const {
    targetIds
  } = strategy;

  if (!(targetIds && targetIds.length)) {
    return targetBranchId;
  }

  const [targetId] = targetIds;

  const {
    rows: targets
  } = await queryDeploymentTargets({
    _doc: targetId
  }, platform);

  if (!(targets && targets.length)) {
    return targetBranchId;
  }

  const [target] = targets;

  const {
    config: {
      branchRef = ''
    } = {}
  } = target;

  if (!branchRef) {
    return targetBranchId;
  }

  const lastSlashIndex = branchRef.lastIndexOf('/');

  if (lastSlashIndex < 0) {
    return targetBranchId;
  }

  targetBranchId = branchRef.substr(lastSlashIndex + 1);

  return targetBranchId;
}
