import {
  get,
  set
} from 'lodash';
import {
  browser as browserMethods,
  queryNodes,
  updateNode
} from '@evdy-consumer/cloudcms-common';

const {
  getBranch,
  getDataPathFromAlpacaPath
} = browserMethods;

export async function getAlreadySelectedItems(selectedQuery) {
  const branch = getBranch();
  const {
    rows = []
  } = await queryNodes(
    selectedQuery,
    { full: true },
    branch
  );

  return rows;
}

export async function setPropertyOnItem(item, path, newValue) {
  const dataPath = getDataPathFromAlpacaPath(path);
  const currentValue = get(item, dataPath);

  if (currentValue === newValue) {
    return;
  }

  const branch = getBranch();
  set(item, dataPath, newValue);
  await updateNode(branch, item);
}

export function getQueryForExisting(field) {
  const {
    highlander: {
      existing: {
        typeQName,
        value
      }
    }
  } = field.options;

  const dataPath = getDataPathFromAlpacaPath(field.path);

  const targetValue = (typeof(value) === 'undefined')
    ? field.getValue()
    : value;

  return {
    _type: typeQName,
    [dataPath]: targetValue
  };
}
