import Ratchet from 'ratchet/web';
import BaseDeleteContentAction from 'app/actions/content/delete-content';
import { deleteContent } from './helpers/delete';
import { actions } from './constants';

function register() {
  const DeleteContentAction = BaseDeleteContentAction.extend({
    doAction: function (actionContext, cb) {
      const {
        data: contentInstances = []
      } = actionContext;

      deleteContent(contentInstances)
        .catch((err) => console.error('Something went wrong deleting content...', err))
        .then(() => {
          if (cb) {
            cb();
          }
        });
    }
  });

  Ratchet.Actions.register(actions.deleteContent, DeleteContentAction);
}

export default register;
