import {
  queryNodesViaGitana
} from '@evdy-consumer/cloudcms-common';

export async function getSchemaForQName(qname, branch) {
  const { rows } = await queryNodesViaGitana({
    _qname: qname
  }, { full: true }, branch);

  return rows[0];
}

export async function getFormOptionsForQName(qname, branch) {
  const schema = await getSchemaForQName(qname, branch);
  const { rows: associationRows } = await queryNodesViaGitana({
    _type: 'a:has_form',
    source: schema._doc
  }, { full: true }, branch);
  const { target } = associationRows[0];
  const { rows: formRows = [] } = await queryNodesViaGitana({
    _doc: target
  }, { full: true }, branch);

  return formRows[0];
}

export default class DashletConfigHelper {
  constructor(options) {
    const { branch } = options;
    
    if (!branch) {
      throw new Error('Missing branch');
    }

    this.branch = branch;
  }

  getSchema() {
    return {
      type: 'object',
      properties: {
        title: {
          type: 'string'
        }
      }
    };
  }

  getFormOptions() {
    return {
      fields: {
        title: {
          label: 'Title'
        }
      }
    };
  }
}
