import axios from 'axios';

function getFileKey({
  path,
  branchId,
  filePrefix
}) {
  return [
    filePrefix,
    branchId,
    path
  ]
    .filter((x) => !!x)
    .join('/')
    .replace(/\/\//g, '/');
}

export async function getUploadUrl({
  file,
  branchId,
  filePrefix,
  fileUploadUrl
}) {
  const {
    path,
    contentType
  } = file;

  const filePath = getFileKey({
    path,
    branchId,
    filePrefix
  });

  const {
    data
  } = await axios.post(fileUploadUrl, {
    path: filePath,
    contentType
  });

  return data;
}

export async function upload(
  file,
  presignedUrl,
  onProgress = () => { }
) {
  const {
    data: fileData,
    contentType
  } = file;

  const {
    data
  } = await axios.put(presignedUrl, fileData, {
    headers: {
      'Content-Type': contentType
    },
    onUploadProgress: (evt) => {
      const {
        lengthComputable,
        loaded,
        total
      } = evt;

      if (lengthComputable) {
        const progress = loaded / total;
        onProgress(progress);
      }
    }
  });

  return data;
}
