import Ratchet from 'ratchet/web';
import {
  browser,
  queryDefinitions
} from '@evdy-consumer/cloudcms-common';
import { defaultWidgetNamespace } from '../constants';

const { getBranch } = browser;

const defaultQuery = {
  query: {
    _qname: {
      $regex: `^${defaultWidgetNamespace}`
    }
  }
};

export function getWidgetContentTypes(query = defaultQuery) {
  const branch = getBranch(Ratchet);

  return queryDefinitions(branch, query);
}
