import { getRefFromQName } from '@evdy-consumer/cloudcms-common';
import BaseDashletConfigHelper, {
  getFormOptionsForQName
} from './base';

const createPrefix = 'create';
const existingPrefix = 'existing';

function getSchemaKey(qname, type) {
  return `${type}:${qname}`;
}

function getSchemaKeyForCreate(qname) {
  return getSchemaKey(qname, createPrefix);
}

function getSchemaKeyForExisting(qname) {
  return getSchemaKey(qname, existingPrefix);
}

function addCreatePropertyToSchema(schema, qname) {
  const createKey = getSchemaKeyForCreate(qname);
  
  schema.properties[createKey] = {
    $ref: getRefFromQName(qname)
  };

  schema.dependencies[createKey] = ['type', 'existing'];
}

function addExistingPropertyToSchema(schema, qname) {
  const editKey = getSchemaKeyForExisting(qname);

  schema.properties[editKey] = {
    type: 'object',
    _relator: {
      associationType: 'a:linked',
      nodeType: qname
    }
  };

  schema.dependencies[editKey] = ['type', 'existing'];
}

class WidgetSelectionDashletConfigHelper extends BaseDashletConfigHelper {
  constructor(options) {
    super(options);

    const { widgetContentTypes } = options;

    if (!widgetContentTypes) {
      throw new Error('Missing widgetContentTypes');
    }

    this.widgetContentTypes = widgetContentTypes;
  }

  getSchema() {
    const schema = {
      title: 'Widget',
      type: 'object',
      required: ['type'],
      properties: {
        existing: {
          type: 'boolean'
        },
        type: {
          type: 'string',
          enum: []
        }
      },
      dependencies: {}
    };
  
    this.widgetContentTypes.forEach(node => {
      const qname = node.getQName();

      schema.properties.type.enum = [
        ...schema.properties.type.enum,
        qname
      ];
  
      addCreatePropertyToSchema(schema, qname);
      addExistingPropertyToSchema(schema, qname);
    });
  
    return schema;
  }

  async getFormOptions() {
    const cfg = {
      fields: {
        existing: {
          rightLabel: 'Select from an existing widget?',
          type: 'checkbox'
        },
        type: {
          label: 'Widget',
          type: 'select',
          optionLabels: this.widgetContentTypes.map(x => x.title)
        }
      }
    };
  
    for(let i = 0, l = this.widgetContentTypes.length; i < l; i += 1) {
      const node = this.widgetContentTypes[i];

      const qname = node.getQName();
      const { title } = node;

      cfg.fields.type.optionLabels = [
        ...cfg.fields.type.optionLabels,
        title
      ];
  
      const createKey = getSchemaKeyForCreate(qname);
      const fieldOptions = await getFormOptionsForQName(qname, this.branch);
      
      cfg.fields[createKey] = {
        ...fieldOptions,
        dependencies: {
          type: qname,
          existing: false
        }
      };

      const editKey = getSchemaKeyForExisting(qname);

      cfg.fields[editKey] = {
        label: node.title,
        type: 'node-picker',
        picker: {
          typeQName: qname,
          upload: false
        },
        dependencies: {
          type: qname,
          existing: true
        }
      };
    }
  
    return cfg;
  }

  isCreate(data) {
    return !data.existing;
  }

  getSelectedWidget(data) {
    const { type: qname } = data;
    const key = getSchemaKeyForExisting(qname);
    return data[key];
  }

  getCreatedWidget(data) {
    const { type: qname } = data;
    const key = getSchemaKeyForCreate(qname);
    return data[key];
  }
}

export default WidgetSelectionDashletConfigHelper;
