import Handlebars from 'handlebars';
import { get as _get } from 'lodash';

function executeHelper(contextKey, ...args) {
  const method = args[0];
  const methodArgs = args.slice(1, args.length - 1);
  const options = args[args.length - 1];
  const context = _get(options, contextKey);
  return context[method].apply(context, methodArgs);
}

const helpers = {
  executeFieldLevelHelper(...args) {
    return executeHelper.apply(null, ['data.root.view.field', ...args]);
  },

  executeOptionsHelper(...args) {
    return executeHelper.apply(null, ['data.root.options.templateHelpers', ...args]);
  },

  getOption(key, hbsOptions) {
    return _get(hbsOptions, `data.root.options.${key}`);
  }
};

Object.keys(helpers).forEach(helperName => {
  Handlebars.registerHelper(helperName, helpers[helperName]);
});
