import Ratchet from 'ratchet/web';
import PublishableHud from 'app/gadgets/project/document/publishable-hud';
import {
  getEnvFromBranch,
  browser as browserMethods
} from '@evdy-consumer/cloudcms-common';

const {
  getBranch
} = browserMethods;

export function isPreviewEnabled(model, pageQName) {
  if (!model.previewEnabled) {
    return false;
  }

  const document = Ratchet.observable('document').get();

  return !!(document && document.getTypeQName() === pageQName);
}

function updateActionsForEnv(actions = []) {
  const branch = getBranch();
  const env = getEnvFromBranch(branch);

  if (env === 'production') {
    return actions;
  }

  return actions.map((action) => {
    const { id } = action;

    if (/unpublish$/.test(id)) {
      return {
        ...action,
        id: id.replace('unpublish', 'unpublish-staging')
      };
    }

    if (/publish$/.test(id)) {
      return {
        ...action,
        id: id.replace('publish', 'publish-staging')
      };
    }

    return action;
  });
}

function register({
  pageQName
}) {
  Ratchet.GadgetRegistry.register('publishable-hud', PublishableHud.extend({
    prepareModel: function(el, model, callback) {
      this.base(el, model, () => {
        model.previewEnabled = isPreviewEnabled(model, pageQName);
        model.actions = updateActionsForEnv(model.actions);

        callback();
      });
    }
  }));
}

export default register;
