import { wait } from '@evdy-consumer/common-helpers/lib/timing';
import {
  browser as browserMethods,
  createWorkflow,
  addWorkflowReference,
  startWorkflow,
  queryNodes,
  getContext,
  findNodes,
  deleteNode,
  constants
} from '@evdy-consumer/cloudcms-common';
import {
  workflowModelId,
  nbrWaitForArchivedAttempts,
  timeBetweenIsArchivedAttempts
} from '../constants';

const {
  getEmailProvider
} = browserMethods;

const {
  namespaces: {
    models: modelsNs,
    widgets: widgetsNs
  },
  traverseFilters: {
    excludeStartNodeFilter
  }
} = constants;

export const findReferencesQuery = {
  query: {
    _type: {
      $regex: `^(${modelsNs}|${widgetsNs}):`
    }
  },
  traverse: {
    depth: 1,
    filter: excludeStartNodeFilter,
    associations: {
      'a:linked': 'INCOMING'
    }
  }
};

export async function findReferences(nodeId, branch) {
  const {
    rows: references
  } = await findNodes(
    nodeId,
    branch,
    findReferencesQuery,
    { full: true }
  );

  return references;
}

export async function getAllNodeReferences(nodes, branch) {
  const allReferences = {};

  for(let i = 0, l = nodes.length; i < l; i += 1) {
    const {
      _doc: nodeId,
      title
    } = nodes[i];

    const references = await findReferences(nodeId, branch);

    if (references.length) {
      allReferences[nodeId] = {
        title,
        references
      };
    }
  }

  return allReferences;
}

export async function isDocumentArchived(node, cfg) {
  const {
    project,
    branch: {
      _doc: branchId
    }
  } = cfg;

  const {
    _doc: projectId
  } = project;

  const {
    _type: qname,
    _doc: documentId
  } = node;

  const {
    documentLifecycle: {
      state
    } = {}
  } = await getContext({
    tokens: {
      branchId,
      documentId,
      projectId,
      qname
    }
  }, project);

  return state === 'archived';
}

export async function waitForDocumentToBeArchived(node, cfg) {
  for(let i = 0; i < nbrWaitForArchivedAttempts; i += 1) {
    if (i !== 0) {
      await wait(timeBetweenIsArchivedAttempts);
    }

    const isArchived = await isDocumentArchived(node, cfg);

    if (isArchived) {
      return;
    }
  }

  throw new Error('Document was not archived ');
}

export async function allDocumentsArchived(unarchivedNodes, cfg) {
  await Promise.all(unarchivedNodes.map((node) => waitForDocumentToBeArchived(node, cfg)));
}

export function createArchivePayload(unarchivedNodes, emailProvider, cfg) {
  const {
    project: {
      _doc: projectId
    },
    branch,
    repository: {
      _doc: repositoryId
    }
  } = cfg;

  const {
    _doc: branchId
  } = branch;

  return {
    context: {
      projectId
    },
    payloadType: 'content',
    payloadData: {
      repositoryId,
      branchId,
      resources: unarchivedNodes.reduce((resources, node) => {
        return {
          ...resources,
          [node.getId()]: node.ref()
        };
      }, {})
    },
    runtime: {
      emailProviderId: emailProvider.getId(),
      applicationId: emailProvider.getApplicationId()
    }
  };
}

export async function getUnarchivedNodes(items, branch) {
  const {
    rows: docs = []
  } = await queryNodes({
    $and: [
      {
        _doc: {
          $in: items.map((item) => item._doc)
        }
      },
      {
        '_features.f:publishable.state': {
          $ne: 'archived'
        }
      }
    ]
  }, {
    full: true
  }, branch);

  return docs;
}

export async function archiveNodes(unarchivedNodes, cfg) {
  const {
    project
  } = cfg;

  if (!unarchivedNodes) {
    throw new Error('Must provide unarchivedNodes');
  }

  if (!cfg) {
    throw new Error('Must provide cfg');
  }

  const emailProvider = await getEmailProvider();
  const payload = createArchivePayload(unarchivedNodes, emailProvider, cfg);
  const instanceId = await createWorkflow(workflowModelId, payload, project);

  await Promise.all(unarchivedNodes.map((node) => {
    return addWorkflowReference(
      instanceId,
      node.ref(),
      project
    );
  }));

  await startWorkflow(instanceId, project);

  await allDocumentsArchived(unarchivedNodes, cfg);
}

export async function deleteNodes(nodeIds, branch) {
  await Promise.all(nodeIds.map((id) => deleteNode(id, branch)));
}
