import { Node } from 'gitana';
import {
  browser as browserMethods
} from '@evdy-consumer/cloudcms-common';
import {
  getAllNodeReferences,
  getUnarchivedNodes,
  archiveNodes,
  deleteNodes
} from './api';
import {
  getModalBody,
  getMessageForReferences
} from './ui';

const {
  getConfirmation,
  getProject,
  getBranch,
  getRepository,
  blockUI
} = browserMethods;

function getBlockingOp(contentInstances) {
  return async () => {
    let deleted = false;

    const branch = getBranch();
    const project = getProject();
    const repository = getRepository();

    const cfg = {
      branch,
      project,
      repository
    };

    const nodeIds = contentInstances.map(doc => doc._doc);

    const nodeReferences = await getAllNodeReferences(contentInstances, branch);

    if (Object.keys(nodeReferences).length) {
      await getConfirmation({
        title: 'Cannot delete referenced documents',
        message: getMessageForReferences(nodeReferences),
        confirmBtnText: 'OK',
        modalCfg: {
          cancel: false
        }
      });

      return deleted;
    }

    const unarchivedDocs = await getUnarchivedNodes(contentInstances, branch);
    const unarchivedNodes = unarchivedDocs.map((doc) => new Node(branch, doc));
    
    await archiveNodes(unarchivedNodes, cfg);

    await deleteNodes(nodeIds, branch);

    deleted = true;

    return deleted;
  };
}

export async function deleteContent(contentInstances) {
  let deleted = false;

  const body = getModalBody(contentInstances);

  const confirmed = await getConfirmation({
    title: 'Delete these content instances?',
    message: body,
    confirmBtnText: 'Delete',
    confirmBtnClass: 'btn-danger',
    modalCfg: {
      cancel: true
    }
  });

  if (!confirmed) {
    return deleted;
  }

  deleted = await blockUI({
    title: 'Please wait...',
    message: 'Starting workflow...',
    fn: getBlockingOp(contentInstances)
  });

  return deleted;
}
