import Ratchet from 'ratchet/web';
import OneTeam from 'oneteam';
import { get } from 'lodash';
import sanitize from 'elasticsearch-sanitize';
import {
  gadgetType,
  iconSize,
  fnMappings,
  defaultMappings
} from './constants';

function register() {
  const { classObject: BasePicker } = Ratchet.GadgetRegistry.find('gitana-node-picker')[0];

  const ConfigurablePicker = BasePicker.extend({
    prepareModel: function(el, model, callback) {
      const {
        columnOverrides,
        columns,
        mappings = {}
      } = model;

      this.mappings = {
        ...defaultMappings,
        ...mappings
      };

      model.columns = columnOverrides || columns;

      this.base(el, model, function() {
        callback();
      });
    },

    iconUri: function (row) {
      const { iconUrlPath } = this.config();

      if (!iconUrlPath) {
        return OneTeam.iconUriForNode(row, {
          size: iconSize,
          name: 'picker'
        });
      }

      const uri = get(row, iconUrlPath);

      if (uri) {
        const url = new URL(uri);
        url.searchParams.set('h', iconSize);
        return url.toString();
      }

      return null;
    },

    columnValue: function (row, item) {
      let value = this.base(row, item);

      const { key }= item;

      const keyMapping = this.mappings[key];
      const fnMapping = fnMappings[keyMapping];

      if (fnMapping) {
        return fnMapping(row);
      }

      if (keyMapping) {
        return get(row, keyMapping);
      }

      return value;
    },
    doGitanaQuery: function(context, model, searchTerm, query, pagination, callback) {
      if (!model || !model.searchFields?.length) {
        this.base(context, model, searchTerm, query, pagination, callback);
        return;
      }

      const options = {
        query: {
          _type: model.typeQName
        }
      };

      if (searchTerm) {
        const escapedSearchTerm = sanitize(searchTerm);

        const search = model.searchFields.reduce((search, field) => {
          return [
            ...search,
            `${field}:*${escapedSearchTerm}*`
          ];
        }, []);
        options.search = search.join(' OR ');
      }

      Chain(this.branch())
        .trap(function(e) {
          console.error(e);
          return false;
        })
        .findNodes(options, pagination)
        .then(function() {
          callback(this);
        })
        .trap(function(e) {
          console.error(e);

          callback({
            totalRows: 0,
            rows: [],
            size: 0
          });

          return false;
        });
    }
  });

  Ratchet.GadgetRegistry.register(gadgetType, ConfigurablePicker);
}

export default register;
