
export function getModalBody(contentInstances = []) {
  let body = '';
  body += '<p>Please confirm that you would like to delete the following content instances:</p>';
  body += '<br/>';
  body += '<ul>';

  for (let i = 0; i < contentInstances.length; i += 1) {
    const {
      title,
      _doc
    } = contentInstances[i];

    const display = title || _doc;

    body += '<li>' + display + '</li>';
  }

  body += '</ul>;';

  return body;
}

export function getMessageForReferences(allNodeReferences = {}) {
  const listItems = Object
    .keys(allNodeReferences)
    .reduce((itemsStr, nodeId) => {
      const {
        title,
        references = []
      } = allNodeReferences[nodeId];

      return `
        ${itemsStr}
        <li>
          <b>${title}</b> is referenced by:
          <ul>
            ${references.map(({ title }) => (`
              <li><b>${title}</b></li>
            `)).join('')}
          </ul>
        </li>
      `;

    }, '');

  return `
    <p>The following documents are still referenced by other documents</p>
    <ul>
      ${listItems}
    </ul>
  `;
}
