import {
  getQNameComponents
} from '@evdy-consumer/cloudcms-common';

export function getWidgetData(data) {
  const { name } = getQNameComponents(data.type);
  return data[name];
}

export async function getDashletConfig(dashletConfigHelper, observableHolder, data, title = 'Custom Widget') {
  const [schema, formCfg] = await Promise.all([
    dashletConfigHelper.getSchema(),
    dashletConfigHelper.getFormOptions()
  ]);

  return {
    title,
    data,
    schema,
    options: formCfg,
    observableHolder
  };
}
