import Ratchet from 'ratchet/ratchet';

function register(options) {
  const StopWorkflowsAction = Ratchet.AbstractAction.extend({
    defaultConfiguration: function () {
      const config = this.base();

      config.title = 'Stop Workflows';
      config.iconClass = 'glyphicon glyphicon-remove';

      return config;
    },

    setActionTitle: function (title) {
      if (typeof $ !== 'undefined') {
        // eslint-disable-next-line no-undef
        $('a[data-document-action-key="stop_workflows"]').text(title);
      }
    },

    execute: function (config, actionContext, callback) {
      const documentId = actionContext.document._doc;
      const branchId = actionContext.document.getBranchId();
      const stopWorkflowsUrl = `${options?.stopWorkflowsUrl}?documentId=${documentId}&branchId=${branchId}`;
      this.setActionTitle(`${config.title}...`);

      const confirmed = confirm(
        'Do you really want to stop all the current Workflows for this document?'
      );

      if (!confirmed) {
        return false;
      }

      return fetch(stopWorkflowsUrl)
        .then(() => {
          this.setActionTitle(`${config.title} `);
          callback();
        })
        .catch((error) => {
          this.setActionTitle(config.title);
          alert(`Failed to stop workflows ${error.message}`);
        });
    }
  });

  Ratchet.Actions.register('stop_workflows', StopWorkflowsAction);
}

export default register;
