import Ratchet from 'ratchet/ratchet';
import BaseDeleteDocumentAction from 'app/actions/document/delete-document';
import { isInternalType } from '@evdy-consumer/cloudcms-common';
import { deleteContent } from './helpers/delete';
import { actions } from './constants';

function register() {
  const DeleteDocumentAction = BaseDeleteDocumentAction.extend({
    doAction: function (actionContext, callback) {
      const { document } = actionContext;
      const type = document.getTypeQName();

      if (isInternalType(type)) {
        let deleted = false;

        deleteContent([document])
          .then(() => deleted = true)
          .catch((err) => {
            console.error('Something went wrong deleting content...', err);
          })
          .then(() => {
            if (deleted) {
              window.history.back();
            } else if (callback) {
              callback();
            }
          });

        return;
      }

      this.base(actionContext, callback);
    }
  });

  Ratchet.Actions.register(actions.deleteDocument, DeleteDocumentAction);
}

export default register;
