import axios from './axios-instance';
import { wait } from '@evdy-consumer/common-helpers/lib/timing';
import {
  getHeadersFromDriver,
  withAuthRetry
} from './helpers';

export async function waitForJobsToFinish(jobIds, projectId, driverProvider) {
  const jobs = jobIds.slice(0);

  while(jobs.length > 0) {
    for(let i = jobs.length - 1; i >= 0; i -= 1) {
      const jobId = jobs[i];

      await withAuthRetry(async () => {
        const {
          state,
          log_entries: logEntries = []
        } = await getJob(jobId, driverProvider);
  
        if (state === 'ERROR') {
          throw new Error(`Job ${jobId} failed: ${JSON.stringify(logEntries, null, 2)}`);
        }
  
        if (state === 'FINISHED') {
          jobs.splice(i, 1);
        }
      }, driverProvider);
    }

    if (jobs.length) {
      await wait(30000);
    }
  }
}

export async function getJob(id, driverProvider) {
  return withAuthRetry(async () => {
    const driver = driverProvider.getDriver();
    const headers = getHeadersFromDriver(driver);

    const {
      data: job
    } = await axios.get(`${driver.baseURL}/jobs/${id}`, {
      headers
    });
  
    return job;
  }, driverProvider);
}

export async function getJobs(driverProvider, options = {}) {
  return withAuthRetry(async () => {
    const driver = driverProvider.getDriver();
    const headers = getHeadersFromDriver(driver);

    const {
      data: {
        rows,
        offset,
        total_rows: totalRows
      }
    } = await axios.post(`${driver.baseURL}/jobs/query`, {}, {
      headers,
      params: {
        full: true,
        limit: 10,
        sort: {
          'started_timestamp.ms': -1
        },
        ...options
      }
    });

    return {
      rows,
      offset,
      totalRows
    };
  }, driverProvider);
}
