export async function deleteNode(id, branch) {
  await withAuthRetry(async () => {
    const driver = branch.getDriver();
    const baseUrl = getBaseUrl(driver);
    const url = `${baseUrl}${branch.getUri()}/nodes/${id}`;

    await axios.delete(url, {
      headers: getHeadersFromDriver(driver)
    });
  }, branch);
}

export async function findNodes(id, branch, payload = {}, params = {}) {
  return withAuthRetry(async () => {
    const driver = branch.getDriver();
    const baseUrl = getBaseUrl(driver);
    const url = `${baseUrl}${branch.getUri()}/nodes/${id}/find`;
    const finalUrl = GetCMSApiUrl(url);

    const {
      data: {
        rows,
        total_rows: totalRows
      }
    } = await axios.post(finalUrl, payload, {
      headers: getHeadersFromDriver(driver),
      params
    });

    return {
      rows,
      totalRows
    };
  }, branch);
}

export async function paginateNodes(options) {
  const {
    onNode,
    ...rest
  } = options;

  await paginate({
    execute: ({
      query,
      pagination,
      driverProvider: branch
    }) => queryNodes(query, pagination, branch),
    onItem: onNode,
    ...rest
  });
}

export async function queryDefinitions(branch, options) {
  const { query } = options;

  const {
    rows
  } = await queryNodes(query, { full: true, limit: 9999 }, branch);

  return rows.map((x) => ({
    ...x,
    getQName: () => x._qname
  }));
}

export function GetCMSApiUrl(url) {
  const apiUrlNonUS = 'api.cloudcms';
  const apiUrlUS1 = 'api.us1.cloudcms';

  if (!url.includes(apiUrlNonUS)) {
    return url;
  }

  const finalUrl = url.replace(apiUrlNonUS, apiUrlUS1);

  return finalUrl;
}
