import BaseDashletConfigHelper from './base';

class CloudCmsApiProxySelectionDashletConfigHelper extends BaseDashletConfigHelper {
  constructor(options) {
    super(options);
    this.modalTitle = options.modalTitle || 'CloudCMS Api Proxy Selector';
  }

  getSchema() {
    const schema = {
      title: this.modalTitle,
      type: 'object',
      required: ['result'],
      properties: {
        searchTerm: {
          type: 'string'
        },
        result: {
          title: 'Results',
          type: 'string',
          enum: []
        }
      }
    };

    return schema;
  }

  async getFormOptions() {
    const cfg = {
      fields: {
        searchTerm: {
          label: 'Search',
          type: 'text',
          helper: 'Enter a search term and press Enter'
        },
        result: {
          type: 'radio',
          removeDefaultNone: true,
          useDataSourceAsEnum: false,
          validate: false,
          hideInitValidationError: true
        }
      }
    };

    return cfg;
  }
}

export default CloudCmsApiProxySelectionDashletConfigHelper;
