import Ratchet from 'ratchet/web';
import {
  createNode,
  readNode,
  updateNode,
  browser as browserMethods
} from '@evdy-consumer/cloudcms-common';
import { getDashletConfig } from './dashlet';
import { apiProxyRequest } from './api-proxy-search';
import {
  SelectWidgetDashletConfigHelper,
  CloudCmsApiProxySelectionDashletConfigHelper,
  EditWidgetDashletConfigHelper
} from './dashlet-config';
import { getWidgetContentTypes } from './filter';

const { getBranch, launchAlpacaModal, blockUI } = browserMethods;

export function openModal(dashletConfig) {
  return launchAlpacaModal(dashletConfig);
}

function showErrorModal() {
  Ratchet.showModal(
    {
      title: 'Something went wrong'
    },
    function setup($div, cb) {
      const $body = $div.find('.modal-body');
      $body.html('Please try again later');
      cb();
    }
  );
}

export function openWidgetSelectionModal({
  widgetTypeQuery,
  widgetContentTypes
} = {}) {
  let contentTypesPromise;
  let dashletConfigHelper;

  if (widgetContentTypes) {
    contentTypesPromise = Promise.resolve(widgetContentTypes);
  } else {
    contentTypesPromise = getWidgetContentTypes(widgetTypeQuery);
  }

  const branch = getBranch();

  const onSubmit = (data) => {
    const { type } = data;

    if (!dashletConfigHelper.isCreate(data)) {
      const { id } = dashletConfigHelper.getSelectedWidget(data);

      return readNode(id, branch).then((node) => ({
        type,
        node
      }));
    }

    const widgetData = dashletConfigHelper.getCreatedWidget(data);

    return createNode(branch, {
      _type: type,
      ...widgetData
    })
      .then((id) => readNode(id, branch)) // TODO: Update createNode to return the node to avoid the extra readNode() op
      .then((node) => ({
        type,
        node
      }))
      .catch((err) => {
        console.error(err);

        showErrorModal();
      });
  };

  return contentTypesPromise
    .then((widgetContentTypes) => {
      dashletConfigHelper = new SelectWidgetDashletConfigHelper({
        widgetContentTypes,
        branch
      });

      return getDashletConfig(dashletConfigHelper, Ratchet);
    })
    .then((dashletConfig) => openModal(dashletConfig))
    .then((data) =>
      blockUI({
        title: 'Saving',
        message: 'Please wait...',
        fn: () => onSubmit(data)
      })
    )
    .catch(console.error);
}

export function openEditWidgetModal(id) {
  const branch = getBranch();

  const onSubmit = (data) => {
    return updateNode(branch, {
      ...data,
      _doc: id
    })
      .then(() => {
        // TODO: Make updateNode() resolve with the updated node and avoid additional call...
        return readNode(id, branch);
      })
      .catch((err) => {
        console.error(err);
        showErrorModal();
      });
  };

  return readNode(id, branch)
    .then((node) => {
      const qname = node.getTypeQName();
      const dashletConfigHelper = new EditWidgetDashletConfigHelper({
        qname,
        branch
      });

      return getDashletConfig(
        dashletConfigHelper,
        Ratchet,
        node.json(),
        'Edit'
      );
    })
    .then((dashletConfig) => openModal(dashletConfig))
    .then((data) =>
      blockUI({
        title: 'Saving',
        message: 'Please wait...',
        fn: () => onSubmit(data)
      })
    )
    .catch(console.error);
}

export function openApiSearchSelectorModal(modalTitle, apiUrl) {
  const branch = getBranch();
  const onSubmit = (data) => {
    return data;
  };


  const dashletConfigHelper = new CloudCmsApiProxySelectionDashletConfigHelper({
    branch,
    modalTitle
  });
  return getDashletConfig(dashletConfigHelper, Ratchet)
    .then((dashletConfig) => {
      const postRenderCallback = function (control) {
        const searchTerm = control.childrenByPropertyId['searchTerm'];
        const recipes = control.childrenByPropertyId['result'];
        searchTerm.on('change', function () {
          const searchTermValue = searchTerm.getValue();
          apiProxyRequest({ apiUrl, searchTerm: searchTermValue })
            .then((result) => {
              const values = [];
              const labels = [];
              searchTerm.options.helpers = [
                'Enter a search term and press Enter',
                `Found ${result?.data?.ehmodels_recipes?.length} records`,
                'Please continue search to get other records.'
              ];
              (result?.data?.ehmodels_recipes || []).forEach(
                ({ title, _doc }) => {
                  values.push(`${title}_____${_doc}`);
                  labels.push(title);
                }
              );
              recipes.schema.enum = values;
              recipes.options.optionLabels = labels;
            })
            .catch((error) => {
              console.error(error);
              recipes.schema.enum = recipes.options.optionLabels = [];
              alert(
                'There was an error while requesting Data from CloudCMS Api Proxy',
                error.message
              );
            })
            .finally(() => {
              searchTerm.refresh();
              recipes.refresh();
            });
        });
      };

      return openModal({ ...dashletConfig, postRenderCallback });
    })
    .then((data) =>
      blockUI({
        title: 'Saving',
        message: 'Please wait...',
        fn: () => onSubmit(data)
      })
    )
    .catch(console.error);
}
