import axios from 'axios';
import {
  withAuthRetry,
  getHeadersFromDriver
} from './helpers';

export async function readRelease(id, repo) {
  return withAuthRetry(async () => {  
    const driver = repo.getDriver();
    const url = `${driver.baseURL}${repo.getUri()}/releases/${id}`;
    
    const { data } = await axios.get(url, {
      headers: getHeadersFromDriver(driver)
    });

    return data;
  }, repo);
}

export async function unarchiveRelease(id, data, repo) {
  await withAuthRetry(async () => {
    const driver = repo.getDriver();
    const url = `${driver.baseURL}${repo.getUri()}/releases/${id}/unarchive`;
    await axios.post(url, data, {
      headers: getHeadersFromDriver(driver)
    });
  }, repo);
}

export async function archiveRelease(id, data, repo) {
  await withAuthRetry(async () => {
    const driver = repo.getDriver();
    const url = `${driver.baseURL}${repo.getUri()}/releases/${id}/archive`;
    await axios.post(url, data, {
      headers: getHeadersFromDriver(driver)
    });
  }, repo);
}

export async function deleteRelease(id, repo) {
  await withAuthRetry(async () => {
    const driver = repo.getDriver();
    const url = `${driver.baseURL}${repo.getUri()}/releases/${id}`;
    await axios.delete(url, {
      headers: getHeadersFromDriver(driver)
    });
  }, repo);
}