import axios from './axios-instance';
import {
  getHeadersFromDriver,
  withAuthRetry
} from './helpers';
import { waitForJobsToFinish } from './jobs';

export function getBranchFromPlatform(repoId, branchId, platform) {
  return new Promise((resolve, reject) => {
    platform
      .readRepository(repoId)
      .trap(err => {
        reject(err);
        return false;
      })
      .readBranch(branchId)
      .then(function() {
        resolve(this.clone());
      });
  });
}

export function getBranchFromRepository(branchId, repo) {
  return new Promise((resolve, reject) => {
    repo
      .readBranch(branchId)
      .then(function() {
        resolve(this.clone());
      })
      .trap((err) => {
        reject(err);
        return false;
      });
  });
}

export function getHeadersFromBranch(branch) {
  return getHeadersFromDriver(branch.getDriver());
}

export function queryBranches(query, pagination, repo) {
  return new Promise((resolve, reject) => {
    repo
      .queryBranches(query, pagination)
      .then(function() {
        resolve({
          rows: this.asArray(),
          totalRows: this.totalRows()
        });
      })
      .trap((err) => {
        reject(err);
        return false;
      });
  });
}

export function startCreateBranch(options, repo) {
  return new Promise((resolve, reject) => {
    const {
      rootBranch,
      data
    } = options;

    repo
      .startCreateBranch(
        rootBranch.getId(),
        rootBranch.getTip(),
        data,
        resolve
      )
      .trap((err) => {
        reject(err);
        return false;
      });
  });
}

export async function createBranches(options) {
  const {
    projectId,
    branches,
    repo
  } = options;

  if (!(branches && branches.length)) {
    return;
  }
  
  const jobIds = await Promise.all(
    branches.map(({
      title,
      rootBranch
    }) => startCreateBranch({
      rootBranch,
      data: {
        title,
        workspace: true
      }
    }, repo))
  );

  await waitForJobsToFinish(jobIds, projectId, repo);
}

export async function buildBranchSearchIndex(options) {
  const {
    projectId,
    repo,
    branch
  } = options;

  await withAuthRetry(async () => {
    const driver = repo.getDriver();
    const url = `${driver.baseURL}${branch.getUri()}/search/index/create/start`;
    const { data } = await axios.post(url, {},
      {
        headers: getHeadersFromDriver(driver)
      }
    );

    await waitForJobsToFinish([data._doc], projectId, repo);
  }, repo);
}

export async function deleteBranch(id, repo) {
  await withAuthRetry(async () => {
    const driver = repo.getDriver();
    const url = `${driver.baseURL}${repo.getUri()}/branches/${id}`;
    
    await axios.delete(url, {
      headers: getHeadersFromDriver(driver)
    });
  }, repo);
}

export function getEnvFromBranch(branch) {
  const { title } = branch;

  if (title.startsWith('DEV: ') || title.startsWith('Pre-Production - ')) {
    return 'preproduction';
  }

  if (title.startsWith('PR: ')) {
    return 'prs';
  }

  if (title.startsWith('STAGING: ')) {
    return 'staging';
  }

  return 'production';
}

export async function resyncPublishing(nodeIds, branch) {
  return withAuthRetry(async () => {
    const driver = branch.getDriver();
    const url = `${driver.baseURL}${branch.getUri()}/publishing/resync`;

    const {
      data: {
        _doc: jobId
      }
    } = await axios.post(url, {
      nodeIds
    }, {
      headers: getHeadersFromDriver(driver)
    });

    return jobId;
  }, branch);
}