import Ratchet from 'ratchet/ratchet';
import CreateContentBase from 'app/actions/content/create-content';

function register() {
  Ratchet.Actions.register('create-content', CreateContentBase.extend({
    populateActionContext: function(actionConfig, actionContext, callback) {
      this.base(actionConfig, actionContext, () => {
        actionContext.model.wizardConfig.next = this._onNextClick.bind(this);
        callback();
      });
    },

    _onNextClick: function(div, actionContext, wizard, wizardCfg, cb) {
      const {
        model: {
          control
        }
      } = actionContext;

      if (!control) {
        cb();
        return;
      }

      control.refreshValidationState(true, () => {
        if (control.isValid(true)) {
          cb();
        } else {
          cb(new Error('Form state is invalid'));
        }
      });
    }
  }));
}

export default register;
