export const gadgetType = 'configurable-node-picker';
export const iconSize = 112;

export const fnMappings = {
  'fn:getTypeQName': (row) => row.getTypeQName(),
  'fn:getPaths': (row) => {
    const { _paths } = row;

    return Object.keys(_paths || {})
      .filter(key => key !== 'r:root')
      .map((key) => _paths[key])
      .join('<br/>');
  }
};

export const defaultMappings = {
  title: 'title',
  description: 'description',
  _type: 'fn:getTypeQName',
  path: 'fn:getPaths'
};
