import { memoize } from 'lodash';
import PreviewDocumentAction from 'app/actions/preview/preview-document';
import {
  browser as browserMethods,
  getTargetBranchId
} from '@evdy-consumer/cloudcms-common';
import Ratchet from 'ratchet/ratchet';
import { previewIds } from '../constants';

const {
  getBranch,
  getPlatform
} = browserMethods;

function getQueryParams(branchId, liveBranchId, prodBranchId) {
  if (!branchId || liveBranchId === prodBranchId) {
    return '';
  }

  return `?branch=${branchId}`;
}

const memoizedGetTargetBranchId = memoize(async (branchId) => {
  const platform = Chain(getPlatform());

  try {
    const targetBranchId = await getTargetBranchId(branchId, platform);
    return targetBranchId || branchId;
  } catch (err) {
    console.error(err);
    return branchId;
  }
});

async function getLiveBranchId() {
  const branchId = getBranch()?.getId();
  let previewBranchId;

  if (branchId) {
    previewBranchId = await memoizedGetTargetBranchId(branchId);
  }

  return previewBranchId;
}

function register({
  prodBranchId,
  authoringUrl,
  publishedUrl,
  pageQName
}) {
  async function getUrl(url, queryParams, path = '/') {
    return `${url}${path}${queryParams}`;
  }

  async function getPreviewUrl(configId, document) {
    const {
      data: {
        url: pagePath
      } = {}
    } = document;

    let baseUrl = authoringUrl;
    let branchId = getBranch()?.getId();
    const liveBranchId = await getLiveBranchId();

    if (configId !== previewIds.AUTHORING) {
      baseUrl = publishedUrl;
      branchId = liveBranchId;
    }

    const queryParams = getQueryParams(branchId, liveBranchId, prodBranchId);

    return getUrl(baseUrl, queryParams,pagePath);
  }

  Ratchet.Actions.register('preview-document', PreviewDocumentAction.extend({
    execute: function (actionConfig, actionContext, callback) {
      const { configId } = actionContext;
      const document = actionContext.observable('document').get();

      if (document && document.getTypeQName() === pageQName) {
        return Chain().then(() => {
          getPreviewUrl(configId, document)
            .then((url) => callback(null, url));
        });
      }

      return this.base(actionConfig, actionContext, callback);
    }
  }));

  return getLiveBranchId();
}

export default register;
