import 'core-js';
import 'regenerator-runtime/runtime.js';
import registerUIOverrides from '@evdy-consumer/cloudcms-ui';
import registerWidgetsSection from '@evdy-consumer/cloudcms-field-widgets-section';
import '@evdy-consumer/cloudcms-field-picker-with-create';
import '@evdy-consumer/cloudcms-field-string-builder';
import '@evdy-consumer/cloudcms-field-slugify';
import '@evdy-consumer/cloudcms-field-widget-data';
import '@evdy-consumer/cloudcms-field-ckeditor-with-externals';
import '@evdy-consumer/cloudcms-field-newsletters';
import registerEvdyNewslettersField from '@evdy-consumer/cloudcms-field-evdy-newsletters';
import registerDailyOMNewslettersField from '@evdy-consumer/cloudcms-field-do-newsletters';
import registerFileUploadField from '@evdy-consumer/cloudcms-field-file-upload';
import registerQuizQuestionField from '@evdy-consumer/cloudcms-field-quiz-question';
import registerUuidField from '@evdy-consumer/cloudcms-field-uuid';
import registerCurrencyPriceIds from '@evdy-consumer/cloudcms-field-currency-price-ids';
import '@evdy-consumer/cloudcms-field-eh-template';
import '@evdy-consumer/cloudcms-field-iso-datetime';
import '@evdy-consumer/cloudcms-field-feed-filter';
import '@evdy-consumer/cloudcms-field-hidden-object';
import '@evdy-consumer/cloudcms-field-courseid';
import registerMaxAssociatedCourseIdField from '@evdy-consumer/cloudcms-field-associated-course-id';
import registerRecipeSelectorField from '@evdy-consumer/cloudcms-field-recipe-selector';
import registerCitationBuilderField from '@evdy-consumer/cloudcms-field-citation-builder';
import './plugins/ckeditor/ckeditor-widgets/index.js';
import './plugins/ckeditor/indentblock/index.js';
import './plugins/ckeditor/divarea/index.js';
import './plugins/ckeditor/ckeditor-color/index.js';

function isProd() {
  return process.env.ENV === 'production';
}

registerWidgetsSection({
  pageQName: process.env.PAGE_QNAME
});

registerUIOverrides({
  authoringUrl: process.env.AUTHORING_URL,
  publishedUrl: process.env.PUBLISHED_URL,
  pageQName: process.env.PAGE_QNAME,
  prodBranchId: process.env.PROD_BRANCH_ID,
  stopWorkflowsUrl: process.env.STOP_WORKFLOWS_URL
});

registerEvdyNewslettersField({
  newsletterApiUrl: process.env.NEWSLETTER_API_URL,
  newsletterIdCsv: process.env.NEWSLETTER_ID_CSV
});

registerDailyOMNewslettersField();

registerMaxAssociatedCourseIdField({
  maxCourseIdUrl: process.env.MAX_COURSE_ID_URL
});

registerFileUploadField({
  filePrefix: process.env.FILE_UPLOAD_PREFIX,
  fileUploadUrl: process.env.FILE_UPLOAD_URL,
  fileQName: process.env.FILE_QNAME,
  pathShouldIncludeBranchId: !isProd()
});

if (process.env.CURRENCY_QNAME) {
  registerCurrencyPriceIds({
    currencyQName: process.env.CURRENCY_QNAME
  });
}

registerQuizQuestionField();

registerUuidField();

registerRecipeSelectorField({
  recipeApiUrl: `${process.env.RECIPE_BASE_URL}/${process.env.RECIPES_BASE_DOMAIN}/v1/proxy/graphql`
});

registerCitationBuilderField({
  getCitationUrl: process.env.GET_CITATION_URL
});
