import {
  connect
} from 'gitana';
import axios from './axios-instance';
import {
  withAuthRetry,
  getHeadersFromDriver
} from './helpers';

export * from './release';
export * from './workflow';
export * from './deployments';
export * from './nodes';
export * from './branch';
export * from './jobs';
export * as apiHelpers from './helpers';

export function getPlatform(config) {
  return new Promise((resolve, reject) => {
    connect(config, function(err) {
      if (err) {
        reject(err);
        return;
      }

      resolve(this.clone());
    });
  });
}

export function getRepository(repoId, platform) {
  return new Promise((resolve, reject) => {
    platform
      .readRepository(repoId)
      .then(function() {
        resolve(this.clone());
      })
      .trap((err) => {
        reject(err);
        return false;
      });
  });
}

export function getProject(projectId, platform) {
  return new Promise((resolve, reject) => {
    platform
      .readProject(projectId)
      .then(function() {
        resolve(this.clone());
      })
      .trap((err) => {
        reject(err);
        return false;
      });
  });
}

export function updateSelf(abstractSelfableObject) {
  return new Promise((resolve, reject) => {
    abstractSelfableObject
      .update()
      .then(function() {
        resolve(this.clone());
      }).
      trap((err) => {
        reject(err);
        return false;
      });
  });
}

export function getContentType(branch, qname) {
  return new Promise((resolve, reject) => {
    if (!qname) {
      reject(new Error('Missing qname'));
      return;
    }

    branch
      .readDefinition(qname)
      .then(function() {
        resolve(this);
      })
      .trap(err => {
        reject(err);
        return false;
      });
  });
}

export function listDefinitions(branch, filter, pagination) {
  return new Promise((resolve, reject) => {
    branch
      .listDefinitions(filter, pagination)
      .then(function() {
        resolve(this.clone());
      })
      .trap(err => {
        reject(err);
        return false;
      });
  });
}

export function getAssociations(definition) {
  return new Promise((resolve, reject) => {
    definition.associations()
      .then(function() {
        resolve(this.clone());
      })
      .trap(err => {
        reject(err);
        return;
      });
  });
}

export async function queryVaults(query, params, branch) {
  return withAuthRetry(async () => {
    const driver = branch.getDriver();
    const url = `${driver.baseURL}/vaults/query`;

    const {
      data: {
        rows,
        total_rows: totalRows
      }
    } = await axios.post(url, query, {
      headers: getHeadersFromDriver(driver),
      params
    });
  
    return {
      rows,
      totalRows
    };
  }, branch);
}

export async function deployModule(id, platform) {
  return withAuthRetry(async () => {
    const driver = platform.getDriver();
    const url = `${driver.baseURL}/oneteam/modules/deploy`;

    const { data } = await axios.post(url, {}, {
      headers: getHeadersFromDriver(driver),
      params: {
        id
      }
    });

    return data;
  }, platform);
}

export async function getContext(payload, driverProvider) {
  return withAuthRetry(async () => {
    const driver = driverProvider.getDriver();
    const url = `${driver.baseURL}/oneteam/context`;

    const {
      data
    } = await axios.post(url, {
      core: true,
      oneteam: true,
      project: true,
      ...payload
    }, {
      headers: getHeadersFromDriver(driver)
    });
  
    return data;
  }, driverProvider);
}
