export default {
  /**
   * Unregister all or an individual event listener
   * @param {evt} name The name of the event to unregister the listener for
   * @param {evt} fn   Optional listener to remove. If not provided, all will be removed
   */
  off: function(name, fn) {
    if (fn) {
      const events = this._events[name] || [];

      for(let i = events.length - 1; i >= 0; i -= 1) {
        if (events[i] === fn) {
          events.splice(i, 1);
        }
      }
    } else {
      this.base(name);
    }
  },

  once: function(name, fn) {
    this.on(name, () => {
      fn.apply(null, arguments);
      this.off(name, fn);
    });
  }
};
