import {
  isArray,
  isObject,
  isEmpty,
  isUndefined
} from 'lodash';

function isEmptyObject(val) {
  return (isArray(val) || isObject(val)) && isEmpty(val);
}

export default {
  getValue: function() {
    const val = this.base();
    const {
      type
    } = this.schema;

    if (this.isRequired()) {
      return val;
    }

    if (type === 'object') {
      const withoutEmpties = Object
        .keys(val || {})
        .reduce((noEmpties, key) => {
          const childVal = val[key];

          if (isEmptyObject(childVal)) {
            return noEmpties;
          }

          return {
            ...noEmpties,
            [key]: childVal
          };
        }, {});

      if (isEmpty(withoutEmpties)) {
        return;
      }
    }

    if (type === 'array') {
      const withoutEmpties = (val || [])
        .map((item) => {
          if (isEmptyObject(item)) {
            return;
          }

          return item;
        })
        .filter((item) => !isUndefined(item));

      if (isEmpty(withoutEmpties)) {
        return;
      }
    }

    return val;
  }
};
