
import axios from './axios-instance';
import {
  withAuthRetry,
  getHeadersFromDriver
} from './helpers';

/**
 * `getWorkflowsByState` function
 *
 * This function is used to fetch all workflows that are currently in a specific state for a given project and workflow model id.
 *
 * @param {Object} session - The session object representing the current user session. (this should be from cloudcms.connect object)
 * @param {string} projectId - The ID of the project for which to fetch the workflows.
 * @param {string} workflowModelId - The ID of the workflow model to filter the workflows. (https://eh-consumer.cloudcms.net/#/workflowmodels)
 * @param {string} lastEditDate - The date to fetch the workflows for, in the format 'YYYY-MM-DD'.
 * @param {string} state - The state to fetch the workflows for. This could be 'RUNNING', 'COMPLETED', 'FAILED', etc.
 * @returns {Promise<Array>} A promise that resolves to an array of workflow objects. Each object represents a workflow that is currently in the specified state.
 *
 */
export async function getWorkflowsByState(session, projectId, workflowModelId, lastEditDate, state) {
  try {
    const cutoff = Date.parse(lastEditDate);
    let query = {
      'context.projectId': projectId,
      'modelId': workflowModelId,
      'state': state,
      '_system.modified_on.ms': {
        '$lt': cutoff
      }
    };

    let pagination = {
      'limit': 50,
      'skip': 0,
      'sort': {
        '_system.modified_on.ms': -1
      }
    };

    let result = null;
    let workflows = [];

    console.log(`Fetching ${state} workflows for project ${projectId} and model ${workflowModelId}...`);

    do
    {
      result = await session.queryWorkflows(query, pagination);
      //console.log(JSON.stringify(result, null, 2)); //Left for debugging purposes
      for (const row of result.rows) {
        workflows.push(row._doc);
      }

      pagination.skip += pagination.limit;
    }
    while (result.size == pagination.limit);
    console.log(`Found ${workflows.length} ${state} workflows.`);
    return workflows;
  }
  catch (ex)
  {
    console.error(ex);
    throw new Error(ex);
  }
}

export async function getWorkflowsByDocId(
  session,
  projectId,
  workflowModelId,
  docId
) {
  const query = {
    'context.projectId': projectId,
    modelId: workflowModelId,
    [`payloadData.resources.${docId}`]: { '$exists': true },
    state: 'RUNNING'
  };

  const pagination = {
    limit: 50,
    skip: 0,
    sort: {
      '_system.modified_on.ms': -1
    }
  };

  let result = null;
  const workflows = [];

  do {
    result = await session.queryWorkflows(query, pagination);
    for (const row of result.rows) {
      if (row?.payloadData?.resources[docId]) {
        workflows.push(row._doc);
      }
    }
    pagination.skip += pagination.limit;
  } while (result.size == pagination.limit);

  return workflows;
}

/**
 * `deleteWorkflowsById` function
 *
 * This function is used to delete a list of workflows.
 *
 * @param {Object} session - The session object representing the current user session.
 * @param {Array<string>} arrWorkflowsIdsToDelete - An array of workflow IDs to be deleted.
 * @returns {Promise<void>} A promise that resolves when all workflows have been deleted.
 *
 * @example
 * deleteWorkflowsById(session, ['workflowId1', 'workflowId2', 'workflowId3'])
 *   .then(deleted => console.log(`${deleted} workflows deleted`))
 *   .catch(error => console.error(error));
 *
 * @throws Will throw an error if the deletion process fails.
 */
export async function deleteWorkflowsById(session, arrWorkflowsIdsToDelete) {
  if (!Array.isArray(arrWorkflowsIdsToDelete) || arrWorkflowsIdsToDelete.length === 0) {
    throw new Error('arrWorkflowsIdsToDelete must be a non-empty array');
  }

  if (!session) {
    throw new Error('session must be initialized');
  }

  try {
    let deleted = 0;
    const deleteFn = async (id) => {
      console.log(`delete ${id}`);
      deleted += 1;
      await session.deleteWorkflow(id);
    };

    const concurrent = 5;
    for (var i = 0; i < arrWorkflowsIdsToDelete.length; i += concurrent)
    {
      const tasks = arrWorkflowsIdsToDelete.slice(i, i + concurrent).map(id => deleteFn(id));
      await Promise.all(tasks);
    }

    console.log(`Deleted ${deleted} workflows`);

    return deleted;
  }
  catch (ex)
  {
    console.error(ex);
    throw new Error(ex);
  }
}

export async function createWorkflow(modelId, payload, project) {
  return withAuthRetry(async () => {
    const driver = project.getDriver();
    const url = `${driver.baseURL}${project.getUri()}/workflow/create`;

    const {
      data: {
        _doc: id
      }
    } = await axios.post(url, payload, {
      headers: getHeadersFromDriver(driver),
      params: {
        modelId,
        full: true
      }
    });

    return id;
  }, project);
}

export async function getWorkflow(id, project) {
  return withAuthRetry(async () => {
    if (!id) {
      throw new Error('Must provide an id');
    }

    const driver = project.getDriver();
    const url = `${driver.baseURL}/workflow/instances/${id}`;

    const {
      data
    } = await axios.get(url, {
      headers: getHeadersFromDriver(driver)
    });

    return data;
  }, project);
}

export async function updateWorkflow(payload, project) {
  return withAuthRetry(async () => {
    const { _doc: id } = payload;

    if (!id) {
      throw new Error('Workflow does not have _doc set');
    }

    const driver = project.getDriver();
    const url = `${driver.baseURL}/workflow/instances/${id}`;

    const {
      data
    } = await axios.put(url, payload, {
      headers: getHeadersFromDriver(driver)
    });

    return data;
  }, project);
}

export async function addWorkflowReference(workflowId, ref, project) {
  return withAuthRetry(async () => {
    if (!workflowId) {
      throw new Error('Must provide a workflow id');
    }

    if (!ref) {
      throw new Error('Must provide a ref');
    }

    const driver = project.getDriver();
    const url = `${driver.baseURL}/workflow/instances/${workflowId}/resources/add`;

    const {
      data
    } = await axios.post(url, {}, {
      headers: getHeadersFromDriver(driver),
      params: {
        reference: ref
      }
    });

    return data;
  }, project);
}

export async function startWorkflow(workflowId, project) {
  return withAuthRetry(async () => {
    if (!workflowId) {
      throw new Error('Must provide a workflow id');
    }

    const driver = project.getDriver();
    const url = `${driver.baseURL}/workflow/instances/${workflowId}/start`;

    const {
      data
    } = await axios.post(url, {}, {
      headers: getHeadersFromDriver(driver)
    });

    return data;
  }, project);
}


