import { browser as browserMethods } from '@evdy-consumer/cloudcms-common';
import BaseDashletConfigHelper, {
  getSchemaForQName,
  getFormOptionsForQName
} from './base';

const {
  getBranch
} = browserMethods;

class EditWidgetDashletConfigHelper extends BaseDashletConfigHelper {
  constructor(options) {
    super(options);

    const { qname } = options;

    if (!qname) {
      throw new Error('Missing qname');
    }

    this.qname = qname;
  }

  getSchema() {
    return getSchemaForQName(this.qname, getBranch());
  }

  getFormOptions() {
    return getFormOptionsForQName(this.qname, getBranch());
  }
}

export default EditWidgetDashletConfigHelper;
