import Ratchet from 'ratchet/web';
import OneTeam from 'oneteam';
import { Node } from 'gitana';
import handlebars from 'handlebars';
import { browser as browserMethods } from '@evdy-consumer/cloudcms-common';
import {
  getAlreadySelectedItems,
  setPropertyOnItem,
  getQueryForExisting
} from './helpers';
import {
  confirmSelectionModal,
  validationMessages,
  behaviors
} from './constants';

const {
  getConfirmation,
  getBranch
} = browserMethods;

const { messageTemplate } = confirmSelectionModal;
const { othersSelected } = validationMessages;

const confirmMessageTemplate = handlebars.compile(messageTemplate);
const othersSelectedTemplate = handlebars.compile(othersSelected);

function isUsingHighlanderMixin(options = {}) {
  const { highlander } = options;

  return !!highlander;
}

function getNewValue(schema, value) {
  const { type } = schema;

  if (type === 'boolean') {
    return !value;
  }

  throw new Error(`type '${type}' not supported`);
}

async function confirmMakeSelected(field, currentlySelectedItems) {
  const {
    title,
    confirmBtnClass,
    confirmBtnText
  } = confirmSelectionModal;

  const {
    highlander: {
      schemaTitle
    }
  } = field.options;

  const currentDefaultTitles = currentlySelectedItems.map((x) => x.title).join(',');
  const newValue = getNewValue(field.schema, field.getValue());

  const message = confirmMessageTemplate({
    title: currentDefaultTitles,
    schemaTitle,
    newValue
  });

  const confirmed = await getConfirmation({
    title,
    message,
    confirmBtnText,
    confirmBtnClass,
    modalCfg: {
      cancel: true
    }
  });

  if (confirmed) {
    await Promise.all(currentlySelectedItems.map(
      (item) => (
        setPropertyOnItem(item, field.path, newValue)
      )
    ));
  }

  return confirmed;
}

function showOthersModal(otherMatchingNodes) {
  const branch = getBranch();

  Ratchet.showModal({
    title: 'Conflicting documents'
  }, function setup($div, cb) {
    const otherDocumentPages = otherMatchingNodes.map((rawNode) => {
      const {
        title
      } = rawNode;

      const gNode = new Node(branch, rawNode);
      const uri = OneTeam.linkUri(Ratchet, gNode);

      return `
        <li>
          <a href="${uri}" target="_blank">${title}</a>
        </li>
      `;
    });

    const $body = $div.find('.modal-body');
    $body.html(`
      <ul>
        ${otherDocumentPages}
      </ul>
    `);

    cb();
  });
}

async function validate(field) {
  const top = field.top();
  const isSelected = field.getValue();
  let validationInfo = {
    status: true
  };

  if (!isSelected) {
    return validationInfo;
  }

  const {
    data: {
      _doc: id
    } = {}
  } = top;

  const {
    highlander: {
      behavior
    }
  } = field.options;

  const query = getQueryForExisting(field);
  const alreadySelectedItems = await getAlreadySelectedItems(query);
  const others = alreadySelectedItems.filter((x) => x._doc !== id);

  if (others.length) {
    let isValid = false;

    if (behavior === behaviors.updateExisting) {
      isValid = await confirmMakeSelected(field, others);
    } else {
      showOthersModal(others);
    }

    if (!isValid) {
      const label = field.options.label || field.options.rightLabel || field.path;

      validationInfo = {
        status: false,
        message: othersSelectedTemplate({
          field: label
        })
      };
    }
  }

  return validationInfo;
}

/**
 * This method is SUPER janky, but we need a way to validate and block submission,
 * and this is the best way i found... options.validator does not block submission
 */
function replaceTopRefreshValidationState(field) {
  const { path } = field;
  const top = field.top();
  const alreadyReplaced = !!top.__highlanderFields__;
  const highlanderFields = top.__highlanderFields__ || {};
  top.__highlanderFields__ = highlanderFields;

  highlanderFields[path] = field;

  if (alreadyReplaced) {
    return;
  }

  const refreshValidationState = top.refreshValidationState;
  
  top.refreshValidationState = async (validateChildren, cb) => {
    const fields = Object.values(highlanderFields);

    for (let i = 0, l = fields.length; i < l; i += 1) {
      const field = fields[i];

      try {
        field.validation.custom = await validate(field);
      } catch (err) {
        console.error('Unable to validate', err);
    
        field.validation.custom = {
          status: false,
          message: validationMessages.error
        };
      }
    }

    refreshValidationState.call(top, validateChildren, cb);
  };
}

export default {
  constructor: function(domEl, data, options, schema, viewId, connector, errorCallback) {
    this.base(domEl, data, options, schema, viewId, connector, errorCallback);

    if (!isUsingHighlanderMixin(this.options)) {
      return;
    }

    const {
      highlander: {
        existing,
        schemaTitle,
        behavior = behaviors.invalidOnly
      }
    } = this.options;

    if (!existing) {
      throw new Error('Missing `existing`');
    }

    if (!existing.typeQName) {
      throw new Error('Missing `existing.typeQName`');
    }

    if (!schemaTitle) {
      throw new Error('Missing `schemaTitle`');
    }

    const { type } = this.schema;

    if (behavior === behaviors.updateExisting && type !== 'boolean') {
      throw new Error(`You cannot use behavior ${behavior} with type ${type}.
       Only 'boolean' is supported`);
    }
  },

  setup() {
    if (isUsingHighlanderMixin(this.options)) {
      this.on('ready', () => {
        replaceTopRefreshValidationState(this);
      });
    }

    this.base();
  }
};
