import {
  getRefDataForNode,
  queryNodes
} from '@evdy-consumer/cloudcms-common';

async function getDefaultValue(defaultsConfig, branch) {
  let defaultKeys = Object.keys(defaultsConfig || {});
  let defaults = [];

  if (defaultKeys.length > 0) {
    if(defaultKeys.length > 1) {
      defaultKeys = defaultKeys.sort((a, b) => defaultsConfig[a]?.order - defaultsConfig[b]?.order);
    }

    for (let i = 0, l = defaultKeys.length; i < l; i += 1) {
      const key = defaultKeys[i];
      const {
        query,
        quantity = 1
      } = defaultsConfig[key];

      try {
        const { rows } = await queryNodes(
          query,
          {
            full: true
          },
          branch
        );

        const refs = rows
          .slice(0, quantity)
          .map((row) => getRefDataForNode(row, branch));

        defaults = [
          ...defaults,
          ...refs
        ];
      } catch (err) {
        console.error(err);
      }
    }
  }

  return defaults;
}

export {
  getDefaultValue
};
