import Ratchet from 'ratchet/web';
import {
  gadgetType
} from './constants';

function register() {
  const ehRegex = /^(ehmodels|ehwidgets):.+/;
  const hiddenTypes = [
    'ehmodels:base'
  ].reduce((types, qname) => {
    return {
      ...types,
      [qname]: true
    };
  }, {});

  const { classObject: BaseContentTypes } = Ratchet.GadgetRegistry.find(gadgetType)[0];

  const ContentTypesGadget = BaseContentTypes.extend({
    prepareModel: function (el, model, callback) {
      this.base(el, model, () => {
        model.types = model.types.filter((type) => {
          const { qname } = type;
          return ehRegex.test(qname) && !hiddenTypes[qname];
        });

        callback();
      });
    }
  });

  Ratchet.GadgetRegistry.register(gadgetType, ContentTypesGadget);
}

export default register;
