import { internalNamespaces } from './constants';

export function getQNameComponents(qname) {
  const [namespace, name] = qname.split(':');

  return {
    namespace,
    name
  };
}

export function convertQNameForRef(qname) {
  return qname.replace(':', '/');
}

export function getRefFromQName(qname) {
  return `qname://${convertQNameForRef(qname)}`;
}

export function getQName(local, namespace) {
  return `${namespace}:${local}`;
}

export function isInternalType(qname) {
  const { namespace } = getQNameComponents(qname);
  return internalNamespaces.has(namespace);
}
