const { get } = require('lodash');

// Note: Gitana shims `window` so can't depend on that alone...
export const isBrowser = typeof window !== 'undefined'
  && typeof document !== 'undefined';

export function getCookies(cookie) {
  return cookie
    .split(';')
    .reduce((cookies, x) => {
      const [key, value] = x.split('=');

      return {
        ...cookies,
        [key.trim()]: value
      };
    }, {});
}

function getCloudcmsOrigin(origin) {
  console.log('location origin', origin);
  if (origin.includes('eh-consumer')) {
    return 'https://eh-consumer.us1.cloudcms.net';
  }

  if (origin.includes('api.cloudcms')) {
    return 'https://api.us1.cloudcms.com';
  }

  return origin;
}

export function getHeadersFromDriver(driver) {
  if (isBrowser) {
    const cookies = getCookies(document.cookie);

    return {
      'x-cloudcms-origin': getCloudcmsOrigin(window.location.origin),
      'x-csrf-token': cookies['CSRF-TOKEN'],
      'x-requested-with': 'XMLHttpRequest'
    };
  }

  return driver.getHttpHeaders();
}

export function refreshAuth(driverProvider) {
  return new Promise((resolve, reject) => {
    const driver = driverProvider.getDriver();

    driver.refreshAuthentication((err) => {
      if (err) {
        reject(err);
        return;
      }

      resolve();
    });
  });
}

export async function paginate(options) {
  const defaultUpdateSkip = (offset, rowCount) => offset + rowCount;

  const {
    execute,
    query,
    onItem,
    branch,
    limit,
    updateSkip = defaultUpdateSkip,
    pagination = {}
  } = options;

  let hasMore = true;
  let skip = 0;

  while (hasMore) {
    const {
      rows,
      totalRows,
      offset
    } = await execute({
      query,
      pagination: {
        full: true,
        limit,
        ...pagination,
        skip
      },
      driverProvider: branch
    });

    const rowCount = rows.length;

    for (let i = 0; i < rowCount; i += 1) {
      const remaining = totalRows - offset - i - 1;
      await onItem(rows[i], remaining);
    }

    skip = updateSkip(offset, rowCount);

    if (skip === -1) {
      skip = defaultUpdateSkip(offset, rowCount);
    }

    hasMore = skip < totalRows;
  }
}

export async function withAuthRetry(fn, driverProvider) {
  try {
    return fn();
  } catch (err) {
    const status = get(err, 'response.status');

    if (status === 401) {
      await refreshAuth(driverProvider);
      return fn();
    } else {
      throw err;
    }
  }
}
