import $ from 'jquery';
import Ratchet from 'ratchet/web';
import OneTeam from 'oneteam';
import BaseUploadDocumentAction from 'app/actions/documents/upload_document';

function updateModalUI($div) {
  const $footer = $div.find('.modal-footer');
  const $body = $div.find('.modal-body');

  $footer.append('<button class="btn btn-primary pull-right done">Done</button>');
  $footer.append('<button class="btn btn-danger pull-left cancel">Cancel</button>');
  $body.html('');
  $body.append('<div class="form"></div>');
}

function getPostRender({
  doActionCallback,
  $div,
  renderCallback
}) {
  return function postRender(control) {
    $div.off('hidden.bs.modal');
    $div.on('hidden.bs.modal', () => {
      const uploadedFileIds = control
        .getControlByPath('/files')
        .getValue() || [];

      doActionCallback(null, uploadedFileIds);
    });

    $div.find('.done').click(() => {
      $div.modal('hide');
    });

    $div.find('.cancel').click(() => {
      $div.modal('hide');
    });

    $div.modal({
      backdrop: 'static',
      keyboard: false
    });

    renderCallback();
  };
}

function getFormConfig({
  actionContext,
  $div,
  renderCallback,
  doActionCallback
}) {
  const {
    model: {
      maxFileSize
    }
  } = actionContext;

  return {
    data: {},
    schema: {
      type: 'object',
      properties: {
        files: {
          type: 'array',
          items: {
            type: 'string'
          }
        }
      }
    },
    options: {
      fields: {
        files: {
          type: 'file-upload',
          upload: {
            maxFileSize
          }
        }
      }
    },
    postRender: getPostRender({
      $div,
      renderCallback,
      doActionCallback
    })
  };
}

function register() {
  const UploadDocumentsAction = BaseUploadDocumentAction.extend({
    doAction: function (actionContext, parentFolderPath, typeQName, ratchet, callback) {
      Ratchet.fadeModal({
        title: 'Upload Documents',
        cancel: false
      }, (div, renderCallback) => {
        const $div = $(div);

        updateModalUI($div);

        const formConfig = getFormConfig({
          actionContext,
          renderCallback,
          $div,
          doActionCallback: callback
        });

        const $form = $div.find('.form');
        OneTeam.formCreate($form, formConfig, actionContext);
      });
    }
  });

  Ratchet.Actions.register('upload_document', UploadDocumentsAction);
}

export default register;
